/* Change colors in this file to style to your needs on a global level. 
Certain colors are used for different and multiply types of elements. 
For greater control over colors visit each components .scss file. */

/* Global color settings */

/* Static SCSS variables (for backward compatibility only) */
$buttonColor: #000C66;
$buttonHover: #000A44;
$topButtonHover: #000;

/* CSS variable setup (this controls your theme dynamically) */
:root {
  --buttonColor: #000C66;      /* Light mode main button color */
  --buttonHover: #000A44;      /* Light mode hover color */
  --topButtonHover: #000;      /* Light mode scroll-to-top hover */
   --progressBarBackground: #e6e6e6;   /* light gray track */
  --progressBarFill: #000c669f;         /* purple/blue fill */
  --iconColor: #868e96;   /* grayish for light mode */
  --iconbuttonColor: #000c66af; /* blue accent */
}

[data-theme="dark"] {
  --buttonColor: #999000;      /* New dark mode main button */
  --buttonHover: #a0a000;      /* Slightly darker hover version */
  --topButtonHover: #fff;      /* Scroll-to-top hover color */
  --progressBarBackground: #22264b;   /* darker gray track */
  --progressBarFill: #d4b100c6;         /* yellow fill (matches your dark theme accent) */
  --iconColor: #868e96;   /* yellow for dark mode */
  --iconbuttonColor: #d4b100c6; /* match your dark accent */
}


